body {
  background: white;
}

.chat-container {
  background: white;
  width: 70vw;
  height: 30vw;
  margin: 3em auto;
  border-radius: 15px;
  padding: 20px 10px;
  color: black;
  border: 3px solid rgb(36, 110, 194);
}

.message {
  border: 1px solid black;
  border-radius: 5px;
  width: 25%;
  padding: 0.5em;
  margin: 0.5em 20em;
}

.header {
  margin: 0.5em;
  font-size: 2em;
  text-align: center;
}

.create-message input[type="text"] {
  margin: 2.5em 2em;
  width: 30%;
  outline: blue;
  border: none;
}

.menudropdown {
}

.sendButton {
  display: inline-block;
}

.input_chat {
  width: 3%;
}
