.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#name {
  color: blue;
  font-style: bold;
  font-family: Georgia, "Times New Roman", Times, serif;
  text-align: center;
  font-size: 3em;
  margin-bottom: 15%;
  margin-top: 15%;
}

#accordian {
  width: 50%;
  text-align: center;
  margin: 0 auto;
}

.cs {
  clear: right;
  float: left;
  margin-top: 15%;
  margin-bottom: 10%;
  margin-right: 10%;
  width: 40%;
}

#cards {
  margin-left: 10%;
}
